.preview {
  width: 352px;
  min-height: 200px;
  margin-right: 50px;
  margin-top: 20px;
}

.color-values-area {
  display: flex;
  justify-content: space-between;
  width: 352px;
  margin-top: 10px;
}

.color-value {
  min-height: 30px;
  min-width: 172px;
  background: white;
  border: 1px solid $content-background;
  display: flex;
  justify-content: center;
  align-items: center;

  span {
    font-size: 16px;
    font-weight: 400;
  }
}

.preview_buttons {
  display: flex;
  justify-content: space-between;
  width: 352px;
  margin-top: 15px;
  margin-bottom: 20px;

  button {
    min-width: 172px;
    border: none;
    background: $button-background-purple;
    color: white;
    height: 32px;
    font-size: 14px;

    &:hover {
      background: darken($color: $button-background-purple, $amount: 10);
      cursor: pointer;
    }
  }
}

@media only screen and (max-width: 600px) {
  .preview {
    margin: 20px auto;
  }
  .color-values-area{
      margin: auto;
  }
  .preview_buttons{
      margin: 10px auto;
  }
}
