.header {
  background: $header-purple;
  height: 65px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.sing-in-button {
  border: none;
  color: white;
  background: $button-background-purple;
  font-size: 24px;
  font-weight: 400;
  margin: auto 68px;
  border-radius: 5px;
  height: 32px;
  min-width: 111px;
  &:hover {
    background: darken($color: $button-background-purple, $amount: 10);
    cursor: pointer;
  }
}

.secound-header {
  color: #312f2f;
  font-size: 26px;
  font-weight: 400;
  margin-bottom: 59px;
  padding-left: 0px;

  ul {
    list-style-type: none;
    display: flex;

    li {
      margin-right: 20px;
      padding-bottom: 8px;
      cursor: pointer;
      line-height: 32px;
     
      
      &:hover{
        font-size: 32px;
        transition: linear 100ms;
       
      }

      &.selected-page {
        border-bottom: 1px solid #bbb6b6;
      }
    }
  }
}

.logo-title {
  margin: auto 68px;
  font-size: 24px;
  font-weight: 400;
  color: #fff;
}

@media only screen and (max-width: 600px) {
  .sing-in-button
  {
    margin: auto auto;
  }
  .logo-title{
    margin: auto;
  }
}