.colors-table {
  width: 100%;
  text-align: left;

  border-bottom: 1px solid black;
  border-collapse: collapse;

  tr {
    border-bottom: 1px solid #bbb6b6;
    

    td {
      padding-top: 16px;
      padding-bottom: 16px;
      padding-right: 10px;
    }

    th {
      color: #898282;
      font-weight: 400;
    }
  }
}
